import React, { useState } from "react";

import SendIcon from "../../images/send.png";
import ImageIcon from "../../images/image.png";
import Emoji from "./Emoji";

const MessageInput = ({
  user,
  imageUrl,
  imageMessageUrls,
  setImageMessageUrls,
  setImageMessageFiles,
  onlineCount,
  countUnread,
  handleSubmit,
  handleTyping,
  handleUserToggle,
  messageToSubmit,
  setMessageToSubmit,
  setErrorMessage,
}) => {
  const [toggleOpen, setToggleOpen] = useState(true);

  var inputPlaceholder;
  if (countUnread === 0) {
    inputPlaceholder = user ? `Welcome ${user.name}` : "Loading...";
  } else if (countUnread === 1) {
    inputPlaceholder = "1 unread message...";
  } else {
    inputPlaceholder = `${countUnread} unread messages...`;
  }

  var submitButtonStyle = { opacity: "0%" };
  if (messageToSubmit !== "" || imageMessageUrls.length !== 0) {
    submitButtonStyle = { opacity: "60%" };
  } else {
    submitButtonStyle = { opacity: "0%" };
  }

  const handleImagesMessageChange = ({ target }) => {
    var acceptImageFiles = [];
    var acceptImageUrls = [];
    var unacceptCount = 0;

    for (var i = 0; i < target.files.length; i++) {
      const type = target.files[i].type;

      if (
        type === "image/jpeg" ||
        type === "image/png" ||
        type === "image/svg+xml" ||
        type === "image/gif"
      ) {
        acceptImageFiles.push(target.files[i]);
        const url = URL.createObjectURL(target.files[i]);
        acceptImageUrls.push(url);
      } else unacceptCount++;
    }

    if (unacceptCount !== 0) {
      setErrorMessage(
        `It looks like you put ${unacceptCount} wrong files. These files must be images`
      );
    }

    if (unacceptCount !== target.files.length) {
      setImageMessageUrls(acceptImageUrls);
      setImageMessageFiles(acceptImageFiles);
    }
  };

  return (
    <div className="main_user_interact">
      <form onSubmit={handleSubmit}>
        <input
          id="user_input_message"
          className="user_input_message"
          value={messageToSubmit}
          placeholder={inputPlaceholder}
          autoComplete="off"
          onChange={({ target }) => {
            setMessageToSubmit(target.value);
            handleTyping(target.value);
          }}
        />
        <button
          style={submitButtonStyle}
          disabled={messageToSubmit === "" && imageMessageUrls === 0}
          className="user_submit_message"
          type="submit"
        >
          <img className="send_icon" src={SendIcon} alt="image_icon" />
        </button>
      </form>

      {imageMessageUrls.length === 0 ? null : (
        <div className="user_input_image">
          {imageMessageUrls.map((url, index) => (
            <img
              key={index}
              className="image_message"
              src={url}
              alt="test_image"
            />
          ))}
          <button
            className="user_input_image_close"
            onClick={() => {
              setImageMessageUrls([]);
              setImageMessageFiles([]);
            }}
          >
            X
          </button>
        </div>
      )}

      <label className="open_user_info">
        <p className="open_user_info_toggle_text">
          {toggleOpen ? "Open" : "Close"}
        </p>
        {imageUrl === null ? null : (
          <img
            className="open_user_info_avatar"
            src={imageUrl}
            alt="open_user_info_avatar"
          />
        )}
        <button
          style={{ display: "none" }}
          onClick={(e) => {
            handleUserToggle(e);
            setTimeout(() => {
              setToggleOpen(!toggleOpen);
            }, 500);
          }}
        />
      </label>

      <label className="image_icon_container">
        <img className="image_icon" src={ImageIcon} alt="image_icon" />
        <input
          style={{ display: "none" }}
          type="file"
          accept="image/png, image/jpeg, image/svg+xml, image/gif"
          onChange={handleImagesMessageChange}
          onClick={({ target }) => {
            target.value = null;
          }}
          multiple
        />
      </label>

      <Emoji
        messageToSubmit={messageToSubmit}
        setMessageToSubmit={setMessageToSubmit}
      />

      <p className="online_count_text">{onlineCount} online</p>
    </div>
  );
};

export default MessageInput;
